				"html": "<h3>Open Source</h3>\n<h4><a href=\"https://reactstrap.github.io\">reactstrap</a></h4>\n<p>Easy to use React Bootstrap 4 components. This library contains React components that favor composition and control. The library does not depend on jQuery or Bootstrap javascript. However, Tether is relied upon for advanced positioning of content like Tooltips, Popovers, and auto-flipping Dropdowns.</p>\n<h4><a href=\"https://github.com/eddywashere/generator-ironsmith\">Ironsmith</a></h4>\n<p>A yeoman generator that packages common features for building out a blog with metalsmith, gulp and scss. Also included is a beautiful theme ported from jekyll called pixyll.</p>\n<h4><a href=\"https://github.com/eddywashere/node-stripe-membership-saas\">Node Stripe Membership SAAS</a></h4>\n<p>An express based app for creating a membership/subscription site with <a href=\"https://stripe.com\">Stripe</a>, <a href=\"https://mailgun.com/signup\">Mailgun</a>, mongodb and swig. Inspired by <a href=\"https://github.com/sahat/hackathon-starter\">sahat/hackathon-starter</a> and <a href=\"https://github.com/RailsApps/rails-stripe-membership-saas\">RailsApps/rails-stripe-membership-saas</a>. It also handles stripe webhooks. Featured on <a href=\"https://stripe.com/docs/examples\">https://stripe.com/docs/examples</a>.</p>\n<h4><a href=\"https://github.com/eddywashere/angular-select-text\">angular-select-text</a></h4>\n<p>An angular directive for selecting text content on click. Why? Some people prefer not to add flash to their site to enable copy to clipboard.\nThe next best thing is to make it easier to select a block of content with 1 simple click.</p>\n<hr>\n<h3>Mailgun</h3>\n<h4><a href=\"https://github.com/mailgun/mailgun-js\">Mailgun-js SDK</a></h4>\n<p>A javascript sdk for Mailgun built with webpack, babel &#x26; es6. This can be used in node or in the browser.</p>\n<h4><a href=\"https://github.com/mailgun/node-prelaunch\">Node Prelaunch</a></h4>\n<p>A Mailgun powered landing page for capturing user sign ups while you work on building out your product. Includes <a href=\"https://github.com/mailgun/transactional-email-templates\">responsive transactional email templates</a>, the ability to send post launch emails in batches and a deploy button.</p>\n<hr>\n<h3>Rackspace</h3>\n<h4><a href=\"http://rackerlabs.github.io/canon\">Canon</a></h4>\n<p>Contributed to Rackspace’s front-end framework, Canon.</p>\n<h4><a href=\"https://github.com/eddywashere/passport-keystone\">passport-keystone</a></h4>\n<p>OpenStack Keystone authentication strategy for Passport.js.</p>",
